@charset "utf-8";

.todo-list-template{
  background-color: #fff;
  width: 520px;
  margin: 0 auto;
  margin-top: 3rem;
  box-shadow: 0px 3px 6px #ccc;
}

.title{
  padding: 2rem;
  font-size: 1.2rem;
  text-align: center;
  font-weight: bold;
  background-color: #22b8cf;
  color: #fff;
}
.form-wrapper{
  padding: 1rem;
  border-bottom: 1px solid #33b8cf;
}
.todos-wrapper{
  min-height: 5rem;
}